body{
    background:#191641;
    margin:0;
    padding: 0;
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    color: black;
}

.todo-container{
    background:#BBC7C8;
    text-align: center;
    width: 22rem;
    padding:10px;
    border-radius: 10px;
    margin: auto; 
}

.input-section{
    display: flex;
    margin-top:10px;
}

.todo-container h1{
    padding: 0;
    font-size: 1.5rem;
    font-weight:bold  
}

.input-section input{
    border-radius: 3px;
    border: solid 1px gray;
    margin-left: 10px;
    height: 2.5rem;
    width:16.5rem;
    padding: 10px;
    font-size:14px;
}

.add-btn{
    margin-left: 20px;
    background:#191641 ;
    border-radius: 3px;
    border: transparent;
    color: white;
    height: 2.5rem;
    width: 3.5rem;
    margin-top:0;
}

.todo-list{
    display: flex;
    padding: 10px;
    background:#BBC7C8;
    margin-top: 10px;
    border-radius: 10px;
    align-items: center;
    justify-content: space-between;
    margin: auto;
    width: 22rem;
    margin-top: 10px;
    font-size: 15px;
}

.delete-btn{
    border-radius: 3px;
    border: transparent;
    height: 2.5rem;
    width: 3.5rem;
    background:#191641;
    color: white;
}

.todo-list-completed{
    display: flex;
    padding: 10px;
    background:#4f5d75;
    margin-top: 10px;
    border-radius: 10px;
    align-items: center;
    justify-content: space-between;
    margin: auto;
    width: 22rem;
    margin-top: 10px;
    font-size: 15px;
}


.task{
    display: flex;
    padding-left: 10px;
}

.task input{
    width: 20px;
    height: 25px;
}

.main-container{
    margin-top: 40px;
}

.completed{
    
    text-transform: capitalize;
    margin: 0; 
    padding: 3px;
    padding-left: 10px;
    text-decoration: line-through;
}

.not-completed{
    text-transform: capitalize;
    margin: 0; 
    padding: 3px;
    padding-left: 10px;
}

.user-details{
    color: white;
    font-size: 1.5rem;
}

.login-icon{
    font-size: 1.5rem;
    cursor: pointer;
    color: white;
}


